body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.nav {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 3rem;
    background-color: rgb(0, 0, 0);
}


.logo {
 color: #fff;
}

.nav__list {

    display: -webkit-flex;

    display: flex;
    list-style: none;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.nav__item {
    margin: 0 2rem;
}


.nav__link {
    text-decoration: none;
    color: rgb(255, 255, 255);
    font-weight: bold;
    padding: 1rem;

}

.active {
    background-color: rgb(7, 163, 77);
}
.photos-content {
    text-align:center;
}

.photos {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}


.photo__box {
    width: 30%;
    box-sizing: border-box;
    margin: 2rem;
    padding: 1.5rem;
    text-align: center;

}


.photo__name {
    display: block;
}

.photo__img {
    width: 100%;
}
.posts-content {
    text-align:center;
}

.posts-content h1 {
    margin-bottom: 4rem;
}

.posts {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.post__box {
    width: 45%;
    box-sizing: border-box;
    margin: 2rem;
    padding: 1.5rem;
    box-shadow: 1rem 1rem 4rem rgba(0, 0, 0, 0.527);

}
.users-content {
    text-align:center;
}

.users {
    background-color: #222;
    padding: 5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.user__box {
    min-width: 20%;
    margin: 2rem;
    padding: 2rem;
    color: #fff;
    text-align: center;
    box-shadow: 3px 3px 5px #000;
    background-color: #555;


}

.user__name {
color: #fff;
}

.user__uid {
    color: #fff;
}

.user__email {

    text-decoration: none;
    color: #fff;
    cursor: pointer;
}
body {
    margin: 0;
    padding: 0;
}

.header {
    background-color: aquamarine;
    background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.527),rgba(0, 0, 0, 0.541)), url(/Abd-Alwahab/ReactSheven/static/media/image-2.e3ff4de5.jpeg);
    background-image: linear-gradient(rgba(0, 0, 0, 0.527),rgba(0, 0, 0, 0.541)), url(/Abd-Alwahab/ReactSheven/static/media/image-2.e3ff4de5.jpeg);
    background-size: cover;
    height:  calc(100vh - 4rem);

}

.header-box {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    text-align: center;
    background-color: rgba(255, 166, 0, 0.479);
    padding: 1rem 2rem;
    border-radius: 10px;

}


h1 {
    color: #fff;
    margin: 0 0 2rem 0;
    text-transform: uppercase;
    letter-spacing: 4px;
}

p {
    color: orange;
    font-size: 1.5rem;
    word-spacing: 3px;
    margin-bottom: 2rem;
}


a {
    color: #fff;
    text-decoration: none;
    padding: 1rem 2rem;
    background-color: orange;
    border-radius: 10px;
    display: inline-block;
    font-size: 1.3rem;

}
